.brands{
    background-color: #F1F5F6;
    padding: 20px 0 50px;
    &__text{
        text-align: center;
        span{
            color: #D8DFE2;
            display: block;
            text-transform: uppercase;
            line-height: 100%;
            &:nth-child(1){
                font-size: 100px;
            }
            &:nth-child(2){
                font-weight: 600;
                font-size: 260px;
              
            }
        }
    }
    &__img{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: -120px;
        .img{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            text-align: center;
            width: calc(100% / 6);
            align-items: center;
            img{
                opacity: .65;
                transition: .3s;
            }
            &:hover{
                img{
                    opacity: 1;
                }
               
            }
        }
    }
}